* {
  margin: 0;
  padding: 0;
}
html {
  font-size: 62.5%;
  font-family: "Microsoft YaHei UI", "Microsoft YaHei UI Light";
}
body {
  font-size: 1em;
  width: 100%;
}
h1 {
  display: none;
}
a:link {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
a:active {
  text-decoration: none;
}
/******头部导航栏、工具栏区域START******/
#header {
  width: 100%;
  max-width: 1920px;
  min-width: 1200px;
}
.c-nav {
  width: 100%;
}
.c-nav a:link {
  color: #000;
}
.c-nav a:visited {
  color: #000;
}
.c-nav a:hover {
  color: #00a8ff;
}
.c-nav a:active {
  color: #00a8ff;
}
.c-nav-inner {
  width: 100%;
  font-size: 1.6em;
  background-color: #000000;
  height: 65px;
  color: #ffffff;
  position: relative;
}
a.c-nav-trigger {
  width: 80px;
  height: 100%;
  text-align: center;
  display: inline-block;
  margin-right: 20px;
}
a.c-nav-trigger i {
  display: inline-block;
  vertical-align: middle;
  width: 32px;
  height: 32px;
  text-indent: -10000px;
  background: url("../icon/trigger_icon.png") no-repeat;
}
.c-nav-logo {
  width: 145px;
  height: 100%;
  text-align: center;
  display: inline-block;
  margin-right: 20px;
}
.c-nav-logo i {
  display: inline-block;
  vertical-align: middle;
  width: 135px;
  height: 40px;
  text-indent: -10000px;
  background: url("../icon/yxlogo.png") no-repeat;
}
ul.c-navTop-list {
  width: auto;
  height: 100%;
  display: inline-block;
  text-align: center;
  list-style: none;
}
ul.c-navTop-list li {
  display: inline-block;
  width: 90px;
  height: 65px;
  line-height: 65px;
  vertical-align: middle;
  cursor: pointer;
}
ul.c-navTop-list li:hover {
  color: #00a8ff;
}
ul.c-navTop-list li a:link {
  color: #fff;
}
ul.c-navTop-list li a:visited {
  color: #fff;
}
ul.c-navTop-list li a:hover {
  color: #00a8ff;
}
ul.c-navTop-list li a:active {
  color: #00a8ff;
}
.c-nav-trigger:after,
.c-nav-logo:after,
ul.c-navTop-list:after {
  content: '';
  display: inline-block;
  width: 0;
  height: 100%;
  vertical-align: middle;
}
.tool-right {
  width: auto;
  height: 65px;
  position: absolute;
  right: 20px;
  top: 0;
}
.c-sysName {
  width: auto;
  height: 100%;
  display: table;
  float: left;
  text-align: center;
}
.c-sysName strong {
  display: table-cell;
  font-size: 1.2em;
  width: 200px;
  vertical-align: middle;
}
.c-sysName:before {
  content: '';
  display: table-cell;
  vertical-align: middle;
  width: 21px;
  height: 65px;
  background: url("../icon/sysIcon.png") no-repeat;
}
#siteTools {
  width: 300px;
  height: 100%;
  display: table;
  float: right;
  text-align: center;
}
#siteTools a {
  color: #ffffff;
  display: table-cell;
  width: 80px;
  height: 32px;
  vertical-align: middle;
}
#siteTools a:link {
  color: #ffffff;
}
#siteTools a:hover {
  color: #e60012;
}
#siteTools span {
  display: table-cell;
  width: 5px;
  height: 32px;
  vertical-align: middle;
}
#siteTools #loginControl {
  height: 32px;
  display: table-cell;
  vertical-align: middle;
}
#siteTools #userCenter {
  height: 50px;
  line-height: 50px;
  position: relative;
  top: 6px;
}
#siteTools #userCenter a {
  display: inline-block;
  height: 40px;
  line-height: 40px;
}
#siteTools #userCenter a#userHeadPortrait {
  display: inline-block;
  width: 38px;
  height: 38px;
  border: 1px solid #dedede;
  border-radius: 50%;
  background-size: 100%;
}
/******头部导航栏、工具栏区域END******/
/******头部导航栏浮层START******/
.c-nav-wrapper {
  width: auto;
  max-width: 1200px;
  font-size: 1.4em;
  position: absolute;
  z-index: 9999;
  background-color: #efefef;
  left: 265px;
  box-shadow: 0 0 30px #434343;
}
.nav-inner-wrapper {
  width: auto;
  margin: 0 auto;
  position: relative;
  display: none;
}
.nav-inner-wrapper .wrapper-list {
  display: inline-block;
  vertical-align: top;
  margin: 20px;
  width: 200px;
}
.nav-inner-wrapper .wrapper-list h3 {
  width: 100%;
  font-size: 1.2em;
  height: 30px;
  line-height: 30px;
  border-bottom: 1px solid #dedede;
}
.nav-inner-wrapper .wrapper-list ul.media-list {
  width: 100%;
  list-style: none;
}
.nav-inner-wrapper .wrapper-list ul.media-list li {
  width: 100%;
  height: 30px;
  line-height: 30px;
}
.nav-inner-wrapper:before {
  content: '';
  display: block;
  width: 16px;
  height: 16px;
  position: absolute;
  top: -16px;
  background: url("../icon/arrow_up.png") no-repeat;
}
.nav-inner-wrapper:after {
  content: '';
  display: block;
  clear: both;
}
.nav-inner-wrapper.adSourceWrapper:before {
  left: 45px;
}
.nav-inner-wrapper.adPrice:before {
  left: 135px;
}
.nav-inner-wrapper.paperManage:before {
  left: 225px;
}
.nav-inner-wrapper.niw-active {
  display: block;
}
/******头部导航栏浮层END******/
/******左侧全站导航-分级菜单START******/
#globalLeftNavBox {
  width: 230px;
  font-size: 1.4em;
  position: absolute;
  z-index: 10;
  top: 65px;
  left: -250px;
  transition: all 0.5s ease;
}
#globalLeftNavBox h3 {
  width: 220px;
  margin-left: 5px;
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid #dedede;
}
#globalLeftNavBox .other-enter-box {
  width: 220px;
  margin-left: 5px;
  margin-top: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  border-top: 1px solid #dedede;
  border-bottom: 1px solid #dedede;
}
#globalLeftNavBox .other-enter-box .enter {
  height: 30px;
  line-height: 30px;
}
#globalLeftNavBox .other-info {
  width: 220px;
  margin-left: 5px;
  padding-top: 20px;
  padding-bottom: 20px;
}
#globalLeftNavBox .other-info p {
  height: 30px;
  line-height: 30px;
}
#globalLeftNavBox .ui-widget.ui-widget-content {
  width: 230px;
  background: #efefef;
  min-height: 300px;
}
#globalLeftNavBox .my-icon-option {
  display: inline-block;
  position: relative;
  margin-right: 5px;
}
#globalLeftNavBox.slideOut {
  left: 0;
}
.ui-menu .ui-menu-item-wrapper {
  padding: 10px 10px 10px 30px !important;
}
/******左侧全站导航-分级菜单END******/
/**********页脚START************/
footer#footer {
  width: 100%;
  max-width: 1920px;
  min-width: 1200px;
  height: 60px;
  margin-top: 50px;
  margin-bottom: 50px;
  color: #ffffff;
}
footer#footer .footer-content {
  width: 1200px;
  margin: 0 auto;
  color: #000;
  text-align: center;
}
footer#footer .footer-content .yx-logo {
  text-align: center;
  margin-bottom: 10px;
}
footer#footer .footer-content .yx-logo i.yx-logo-icon {
  display: inline-block;
  width: 135px;
  height: 40px;
  background-image: url("../icon/yxlogo.png");
  text-indent: -100000px;
  vertical-align: middle;
}
footer#footer .footer-content .copyright {
  text-align: center;
  margin-bottom: 10px;
}
/**********页脚END************/
/****** @media screen START *************/
@media screen and (max-width: 1400px) {
  .c-nav-inner {
    font-size: 1.4em;
  }
  ul.c-navTop-list li {
    width: 70px;
  }
  .tool-right .c-sysName {
    width: 202px;
  }
  .tool-right #siteTools {
    width: 240px;
  }
}
/****** @media screen END *************/
